<script setup lang="ts">
import {
  ref, onMounted, onBeforeMount, defineProps, defineEmits, watch, computed, PropType,
} from 'vue';
import { useRouter } from 'vue-router';
import { useToast } from 'primevue/usetoast';
import { useConfirm } from 'primevue/useconfirm';
import { useI18n } from 'vue-i18n';
import Dialog from 'primevue/dialog';
import Divider from 'primevue/divider';
import Button from 'primevue/button';
import InputText from 'primevue/inputtext';
import InputNumber from 'primevue/inputnumber';
import InputSwitch from 'primevue/inputswitch';
import Textarea from 'primevue/textarea';
import AppRecord from '@/models/appRecord';
import FieldGridComponent from '../../../shared/src/components/FieldGridComponent.vue';

// Infrastructure
const router = useRouter();
const confirmVue = useConfirm();

// Props
const props = defineProps({
  appRecord: { type: Object as PropType<AppRecord>, required: true },
});

// Data
const headerText = ref('App settings');

// Emits
const emit = defineEmits({
  close: null,
  save: (record) => true,
});

// Methods

// Event handlers
function cancelClick(): void {
  emit('close');
}

function saveClick(): void {
  emit('save', props.appRecord);
}

function updateVisible(value: boolean): void {
  if (!value) {
    emit('close');
  }
}

// Life cycle hooks
onMounted(() => {
  if (!props.appRecord.id) {
    headerText.value += ' (New)';
  }
});

</script>

<template>
  <div>
    <Dialog
      :visible="true"
      :style="{width: '50rem'}"
      :draggable="false"
      :header="headerText"
      class="pb-0"
      modal
      @update:visible="updateVisible"
    >
      <div>
        <!-- Id -->
        <div class="grid">
          <div class="col">
            <FieldGridComponent title="Id">
              <InputText
                id="appRecordId"
                v-model="appRecord.id"
                size="small"
                aria-labelledby="appRecordId"
                class="w-full"
              />
            </FieldGridComponent>

          </div>
        </div>

        <!-- Title -->
        <div class="grid">
          <div class="col">
            <FieldGridComponent :title="$t('title')"
                                :required="true"
                                labelFor="appRecordTitle" >
              <InputText
                id="appRecordTitle"
                v-model="appRecord.title"
                size="small"
                aria-labelledby="appRecordTitle"
                class="w-full"
              />
            </FieldGridComponent>
          </div>
        </div>

        <div class="grid">
          <div class="col">
            <Divider />
          </div>
        </div>

        <!-- Use workflows Scheduler -->
        <div class="grid">
          <div class="col">
            <FieldGridComponent :title="$t('useWorkflowsScheduler')">
              <InputSwitch v-model="appRecord.useWorkflowsScheduler"></InputSwitch>
            </FieldGridComponent>
          </div>
        </div>

        <!-- Workflow poll interval -->
        <div class="grid">
          <div class="col">
            <FieldGridComponent :title="`${$t('workflowPollInterval')}, ms`"
                                :required="true"
                                labelFor="fld-workflow-poll-interval">

              <InputNumber id="fld-workflow-poll-interval"
                           v-model="appRecord.workflowPollInterval"
                           min="100"
                           size="small"></InputNumber>

            </FieldGridComponent>
          </div>
        </div>

        <div class="grid">
          <div class="col">
            <Divider />
          </div>
        </div>

        <div class="grid">
          <div class="col">
            <label for="appRecordMemo">{{$t('memo')}}</label>
            <Textarea
              id="appRecordMemo"
              v-model="appRecord.memo"
              rows="3"
              aria-labelledby="appRecordMemo"
              class="w-full"
            />
          </div>
        </div>
      </div>
      <template #footer>
        <Button
          :label="$t('cancel')"
          severity="secondary"
          size="small"
          outlined
          @click="cancelClick"
        />
        <Button :label="$t('save')" size="small" @click="saveClick" />
      </template>
    </Dialog>
  </div>
</template>

<style scoped>

</style>
