export default class AppRecord {
  id: string;
  title;
  memo;
  useWorkflowsScheduler: boolean;
  workflowPollInterval: number;

  constructor(param: any) {
    let data: any = {};
    if (param) {
      data = param;
    }

    this.id = data.id;
    this.title = data.title || '';
    this.memo = data.memo || '';
    this.useWorkflowsScheduler = data.useWorkflowsScheduler || false;
    this.workflowPollInterval = data.workflowPollInterval || 1000;
  }

  equals(obj: any): boolean {
    return obj instanceof AppRecord && this.id === obj.id;
  }

  toString(): string {
    return `${this.id}/${this.title}`;
  }
}
